'use client';
import { usePathname } from 'next/navigation';
import { Header, Footer } from './components/layout';
import { Account, Error404, Home, Item, Search, Settings, Watch } from "./components/features";
import { useJson } from './hooks/useJson';

export default function App() {
  const { json, lang } = useJson();
  const path = usePathname();

  if (!json) return null;

  return (
    <>
      {path !== "/watch" && <Header json={json} lang={lang} />}
      {(() => {
        switch (path) {
          case '/': return <Home json={json} lang={lang} />;
          case '/account': return <Account json={json} lang={lang} />;
          case '/search': return <Search json={json} lang={lang} />;
          case '/settings': return <Settings json={json} lang={lang} />;
          case '/item': return <Item json={json} lang={lang} />;
          case '/watch': return <Watch json={json} lang={lang} />;
          default: return <Error404 json={json} lang={lang} />;
        }
      })()}
      {path !== "/watch" && <Footer json={json} lang={lang} />}
    </>
  );
}